<template>
  <div class="poster-list">
    <common-template>
      <h1 class="content__title" slot="title">{{$route.meta.title}}</h1>

      <div class="content__main-operation" slot="header">
        <div class="operation-left">
          <el-button type="primary" size="small" @click="$router.push('/store/manager/cent/add')">新增商品</el-button>
          <el-button size="small" plain @click="batchPublish(1)">批量上架</el-button>
          <el-button size="small" plain @click="batchPublish(0)">批量下架</el-button>
          <el-button size="small" plain @click="handleDelete(0)">批量删除</el-button>
        </div>
        <div class="operation-right">
          <el-input size="small" v-model="req.name" placeholder="请输入内容" @keyup.enter.native="reload">
            <el-button slot="append" icon="el-icon-search" @click="reload()"></el-button>
          </el-input>
        </div>
      </div>

      <div class="content__main-list" slot="list">
        <el-table
          v-loading = "!list"
          ref="multipleTable"
          @selection-change="handleSelectionChange"
          :data="list"
          tooltip-effect="dark"
          style="width: 100%">

          <el-table-column
            type="selection"
            width="55">
          </el-table-column>

          <el-table-column
            min-width="250"
            label="商品信息"
            align="left">
            <template slot-scope="scope">
              <div class="info-item">
                <i class="item__image" :style="{backgroundImage: 'url('+scope.row.thumb+')'}"></i>
                <div class="item__info">
                  <p class="item__info-main" style="font-weight: 500;">{{scope.row.name}}</p>
                </div>
              </div>
            </template>
          </el-table-column>

          <el-table-column
            label="换购条件"
            align="center">
            <template slot-scope="scope">
              <div style="color:red">{{scope.row.energy}}能量<br>0.01元</div>
            </template>
          </el-table-column>

          <el-table-column
            label="库存"
            align="center"
            prop="stock">
          </el-table-column>

          <el-table-column
            label="排序"
            align="center"
            prop="sort">
          </el-table-column>

          <el-table-column
            width="150"
            label="上架状态"
            align="center"
            prop="createTime">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.frozen"
                :active-text="scope.row.frozen ? '已上架' : '已下架'"
                @change = "changeStatus(scope.row.id, scope.row.frozen)">
              </el-switch>
            </template>
          </el-table-column>

          <el-table-column
            width="100"
            label="创建时间"
            align="center"
            prop="createTime">
          </el-table-column>

          <el-table-column
            label="操作"
            width="160"
            align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.row.id)">编辑</el-button>
              <el-button
                type="danger"
                size="mini"
                @click="handleDelete(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>

        </el-table>

        <div style="text-align: center; margin-top: 60px;" v-if="list && list.length>0">
          <el-pagination
            :page-sizes="[10, 20, 30, 40, 50]"
            :page-size="page.pageSize"
            :current-page="page.pageNo"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.totalRows"
            @size-change = "changePageSize"
            @current-change = "changePageNo">
          </el-pagination>
        </div>

      </div>
    </common-template>
  </div>

</template>

<script>
  import CommonTemplate from '@/components/common-template'
  import GoodsApi from '@/api/store.goods.js'

  export default {
    name: 'goods-list',
    components: { CommonTemplate },
    data() {
      return {
        list: null,
        req: {type: 2},
        page: {pageNo:1, pageSize: 20},
        choose_id_arr: [],
      }
    },
    created() {
      this.getList()
    },
    methods: {
      getList() {
        this.req.p = this.page.pageNo
        this.req.s = this.page.pageSize
        GoodsApi.getList(this.req).then((res) => {
          this.list = res.list
          this.list.forEach(item => {
            item.frozen = Boolean(item.frozen)
          })
          this.page = res.page
        }, (err) => {
          this.$message.error(err.msg);
        })
      },
      reload() {
        this.page.pageNo = 1
        this.getList()
      },
      changePageSize(value){ // 修改每页条数size
        this.page.pageNo = 1
        this.page.pageSize = value
        this.list = null
        this.getList()
      },
      changePageNo(value){ // 修改当前页currentPage
        this.page.pageNo = value
        this.list = null
        this.getList()
      },

      handleSelectionChange(val) {
        this.choose_id_arr = val
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
        this.choose_id_arr = rows
      },

      // 编辑
      handleEdit(id) {
        this.$router.push({path:`/store/manager/cent/edit/${id}`})
      },

      // 上下架
      changeStatus(id, flag) {
        GoodsApi.batchPublish([id], !flag?0:1).then((res) => {
          this.$message.success('操作成功');
          this.reload()
        }, (err) => {
          this.$message.error(err.msg);
        })
      },
      // 批量上下架
      batchPublish(flag) { // flag:1上架，0下架
        if (this.choose_id_arr.length === 0) {
          this.$message.error("请至少选择一个商品！");
          return
        }
        let ids = this.choose_id_arr.map(item => item.id)
        this.$confirm('确认'+(!flag?'下架':'上架')+'?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          GoodsApi.batchPublish(ids, !flag?0:1).then((res) => {
            this.$message.success('操作成功');
            this.reload()
          }, (err) => {
            this.$message.error(err.msg);
          })
        }).catch(() => {

        });
      },

      // 批量删除
      handleDelete(id) {
        let ids = [id]
        if (id == 0) { // 批量删除
          if (this.choose_id_arr.length === 0) {
            this.$message.error("请至少选择一项！");
            return
          }
          ids = this.choose_id_arr.map(item => item.id)
        }

        this.$confirm('删除后无法恢复，是否确认？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          GoodsApi.batchDelete(ids).then((res) => {
            this.$message.success('操作成功');
            this.reload()
          }, (err) => {
            this.$message.error(err.msg);
          })
        })
      },

    }
  }
</script>

<style lang="scss">

</style>



