
import http from '@/utils/http'


export default {

  // 列表
  getList : params => {
    return http.post("/picture/list", params)
  },

  // 保存
  save : params => {
    return http.post("/picture/save", params)
  },

  // 批量删除
  batchDelete : ids => {
      return http.post("/picture/delete", ids)
  },

  // 批量显示
  batchPublish : (ids, flag) => {
    return http.post("/picture/publish/" + flag, ids)
  },

  // 广告信息
  getAdvert : () => {
    return http.post("/picture/advert", {})
  },
}



