
import http from '@/utils/http'


export default {

  /**
   * 列表
   */
  getList : params => {
    return http.post("/poster/list", params)
  },

  /**
   * 信息
   * @param id
   * @returns {*|any}
   */
  info : id => {
    return http.post("/poster/"+id+"/info", {})
  },

  /**
   * 保存
   * @param params
   * @returns {*|any}
   */
  save : params => {
    return http.post("/poster/save", params)
  },

  /**
   * 批量删除
   * @param ids
   * @returns {*|any}
   */
  batchDelete : ids => {
    return http.post("/poster/delete", ids)
  },

  /**
   * 批量上下架
   * @param ids
   * @param flag
   * @returns {*|any}
   */
  batchPublish : (ids, flag) => {
    return http.post("/poster/publish/" + flag, ids)
  },

  /**
   * 分类选择列表
   * @returns {*|any}
   */
  getCategoryChooseList: (type) => {
    return http.post("/category/"+type+"/chooselist", {})
  },

  /**
   * 上传图片
   */
  uploadPic: pics => {
    return http.post("/files/upload", pics);
  }

}



