import Router from '@/components/Energy/router'
import Index from '@/components/Energy/index'
import Poster from '@/components/Energy/poster'
import PosterAdd from '@/components/Energy/Add'
import Video from '@/components/Energy/video'
import Picture from '@/components/Energy/picture'
import Category from '@/components/Energy/category'
import HotTag from '@/components/Energy/tag'

export default {
  path: '/energy',
  name: '励志管理',
  icon: 'el-icon-news',
  component: Index,
  hidden_child: false,
  children: [
    {
      name: '励志管理',
      path: 'manager',
      component: Router,
      children: [
        {
          name: '励志文章',
          path: 'poster',
          component: Poster,
          meta: {
            title: "文章列表"
          }
        },
        {
          name: '励志视频',
          path: 'video',
          component: Video,
          meta: {
            title: "视频列表"
          }
        },
        {
          exclude: true,
          path: ':type/add',
          component: PosterAdd,
          meta: {
            title: '添加信息'
          },
        },
        {
          exclude: true,
          path: ':type/edit/:id',
          component: PosterAdd,
          meta: {
            title: '编辑信息'
          },
        },
        {
          name: '励志美图',
          path: 'picture',
          component: Picture,
          meta: {
            title: "美图列表"
          }
        },
        {
          name: '分类管理',
          path: 'category',
          component: Category,
          meta: {
            title: "分类列表"
          }
        },
        {
          name: '热门标签',
          path: 'tag',
          component: HotTag,
          meta: {
            title: "热门标签"
          }
        },
      ],
      redirect: '/energy/manager/poster'
    },
  ],
  redirect: '/energy/manager'
}



