
import http from '@/utils/http'


export default {

  // 列表
  getList : params => {
    return http.post("/advert/list", params)
  },

  // 保存
  save : params => {
    return http.post("/advert/save", params)
  },

  // 批量删除
  batchDelete : ids => {
      return http.post("/advert/delete", ids)
  },

  // 批量显示
  batchPublish : (ids, flag) => {
    return http.post("/advert/publish/" + flag, ids)
  },
}



