<template>
  <div class="member-list">
    <common-template>
      <h1 class="content__title" slot="title">{{$route.meta.title}}</h1>

      <div class="content__main-list" slot="list">
        <el-table
          v-loading = "!list"
          ref="multipleTable"
          :data="list"
          tooltip-effect="dark"
          style="width: 100%">

          <el-table-column
            label="客户信息"
            min-width="300"
            align="left">
            <template slot-scope="scope">
              <div class="info-item">
                <i class="item__image" :style="{backgroundImage: 'url('+scope.row.photo+')'}"></i>
                <div class="item__info">
                  <p class="item__info-main" style="font-weight: 500;">{{scope.row.nickName}}</p>
                </div>
              </div>
            </template>
          </el-table-column>

          <el-table-column
            label="可用能量"
            align="center"
            prop="energy">
          </el-table-column>
          <el-table-column
            label="历史能量"
            align="center"
            prop="totalEnergy">
          </el-table-column>

          <el-table-column
            label="加入时间"
            align="center"
            prop="createTime">
          </el-table-column>
          <el-table-column
            label="最近一次登录时间"
            align="center"
            prop="lastLoginTime">
          </el-table-column>
        </el-table>

        <div style="text-align: center; margin-top: 60px;" v-if="list && list.length>0">
          <el-pagination
            :page-sizes="[10, 20, 30, 40, 50]"
            :page-size="page.pageSize"
            :current-page="page.pageNo"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.totalRows"
            @size-change = "changePageSize"
            @current-change = "changePageNo">
          </el-pagination>
        </div>

      </div>
    </common-template>
  </div>

</template>

<script>
  import CommonTemplate from '@/components/common-template'
  import MemberApi from '../../api/member';

  export default {
    name: 'member-list',
    components: { CommonTemplate },
    data() {
      return {
        list: null,
        req: {nickName: ""},
        page: {pageNo:1, pageSize: 20},
      }
    },
    created() {
      this.getList()
    },
    methods: {
      getList() {
        this.req.p = this.page.pageNo
        this.req.s = this.page.pageSize
        MemberApi.getList(this.req).then((res) => {
          this.list = res.list ? res.list : []
          this.page = res.page
        }, (err) => {
          this.list = []
            this.$message.error(err.msg);
        })
      },
      reload() {
        this.page.pageNo = 1
        this.getList()
      },
      changePageSize(value){ // 修改每页条数size
        this.page.pageNo = 1
        this.page.pageSize = value
        this.list = null
        this.getList()
      },
      changePageNo(value){ // 修改当前页currentPage
        this.page.pageNo = value
        this.list = null
        this.getList()
      },
    }
  }
</script>

<style lang="scss">
  .member-list {

  }
</style>



