import Router from '@/components/Member/router'
import Index from '@/components/Member/index'
import List from '@/components/Member/list'

export default {
  path: '/member',
  name: '微信客户',
  icon: 'el-icon-view',
  component: Index,
  hidden_child: false,
  children: [
    {
      name: '客户管理',
      path: 'manager',
      component: Router,
      children: [
        {
          name: '全部客户',
          path: 'list',
          component: List,
          meta: {
            title: "客户列表"
          }
        },
      ],
      redirect: '/member/manager/list'
    },
  ],
  redirect: '/member/manager'
}



