import Router from '@/components/Store/router'
import Index from '@/components/Store/index'
import Goods from '@/components/Store/goods'
import GoodsAdd from '@/components/Store/add'
import CentGoods from '@/components/Store/cent'
import CentAdd from '@/components/Store/cent_add'
import Orders from '@/components/Store/goods'

export default {
  path: '/store',
  name: '兑换商城',
  icon: 'el-icon-star-off',
  component: Index,
  hidden_child: false,
  children: [
    {
      name: '商城管理',
      path: 'manager',
      component: Router,
      children: [
        {
          name: '兑换商品',
          path: 'goods',
          component: Goods,
          meta: {
            title: "兑换商品"
          }
        },
        {
          exclude: true,
          path: 'goods/add',
          component: GoodsAdd,
          meta: {
            title: '添加换购商品'
          },
        },
        {
          exclude: true,
          path: 'goods/edit/:id',
          component: GoodsAdd,
          meta: {
            title: '编辑商品信息'
          },
        },
        {
          name: '一分钱换购',
          path: 'cent',
          component: CentGoods,
          meta: {
            title: "一分钱换购"
          }
        },
        {
          exclude: true,
          path: 'cent/add',
          component: CentAdd,
          meta: {
            title: '添加一分购商品'
          },
        },
        {
          exclude: true,
          path: 'cent/edit/:id',
          component: CentAdd,
          meta: {
            title: '编辑商品信息'
          },
        },
        {
          name: '订单管理',
          path: 'orders',
          component: Orders,
          meta: {
            title: "订单管理"
          }
        },
      ],
      redirect: '/store/manager/goods'
    },
  ],
  redirect: '/store/manager'
}



