
import http from '@/utils/http'


export default {

  /**
   * 列表
   */
  getList : params => {
    return http.post("/goods/list", params)
  },

  /**
   * 信息
   * @param id
   * @returns {*|any}
   */
  info : id => {
    return http.post("/goods/info/" + id, {})
  },

  /**
   * 保存
   * @param params
   * @returns {*|any}
   */
  save : params => {
    return http.post("/goods/save", params)
  },

  /**
   * 批量删除
   * @param ids
   * @returns {*|any}
   */
  batchDelete : ids => {
    return http.post("/goods/delete", ids)
  },

  /**
   * 批量上下架
   * @param ids
   * @param flag
   * @returns {*|any}
   */
  batchPublish : (ids, flag) => {
    if (flag === 1) { // 上架
      return http.post("/goods/batchPublish", ids);
    }else if(flag === 0){ // 下架
      return http.post("/goods/batchFrozen", ids);
    }
  },

  /**
   * 上传图片
   */
  uploadPic: pics => {
    return http.post("/files/upload", pics);
  }

}



