<template>
  <div class="common-add-content goods-add-content">
    <h1 class="content__title">
      <router-link v-if="poster.type == 1" to="/energy/manager/poster">励志文章 </router-link>
      <router-link v-else-if="poster.type == 2" to="/energy/manager/video">励志视频 </router-link>
      / {{$route.meta.title}}
    </h1>

    <div class="content__main" style="margin-top: 20px;">
      <el-tabs v-model="activeName">
        <el-tab-pane label="① 填写基本信息" class="main-first" name="first" style="padding-bottom: 30px;">
          <div class="section">
            <el-form :rules="rules" ref="EditForm" :model="poster" :label-width="formLabelWidth" style="width: 600px;">
              <el-form-item :label="poster.type==1?'缩略图':'封面图'">
                <ul class="app-image-list">
                  <li class="image-item" v-if="poster.thumb" @click="showPhotoDialog=true">
                    <img :src="poster.thumb" role="presentation" alt="" width="80">
                  </li>
                  <li @click="showPhotoDialog=true" v-else class="el-upload el-upload--picture-card" style="width: 80px;height: 80px;line-height: 1;">
                    <i class="el-icon-plus" style="font-size: 40px;margin-top: 20px;"></i>
                  </li>
                </ul>
              </el-form-item>
              <el-form-item label="分类" prop="cid">
                <el-select size="small" v-model="poster.cid" placeholder="请选择分类">
                  <el-option v-for="item,index in cate_list" :key="index" :label="item.name" :value="item.id"></el-option>
                </el-select>
                <a href="/#/energy/manager/category" class="form__button">新增分类</a>
              </el-form-item>
              <el-form-item label="标题" prop="title">
                <el-input size="small" v-model="poster.title"></el-input>
              </el-form-item>
              </el-form-item>
              <el-form-item label="作者" prop="author">
                <el-input size="small" v-model="poster.author"></el-input>
              </el-form-item>

              <template v-if="poster.type == 2">
                <el-form-item label="视频地址" prop="videoUrl" required>
                  <el-input size="small" v-model="poster.videoUrl"></el-input>
                  <p class="xs-assist-label"><a href="https://shimo.im/doc/ayL4ksdCWgAUZwue?r=08ZY3M" target="_blank" >如何获取原始视频?</a></p>
                </el-form-item>
                <el-form-item label="视频时长" prop="duration" required>
                  <el-input size="small" v-model="poster.duration"></el-input>
                  <p class="xs-assist-label">如：05:30</p>
                </el-form-item>
              </template>

              <el-form-item label="简介" prop="brief" >
                <el-input type="textarea" :rows="5" size="small" v-model="poster.brief"></el-input>
              </el-form-item>
              <el-form-item label="是否推荐" prop="isHot">
                <el-switch v-model="poster.isHot" :active-text="poster.isHot ? '是' : '否'"></el-switch>
              </el-form-item>
              <el-form-item label="排序字段" prop="sort">
                <el-input size="small" v-model="poster.sort"></el-input>
                <p class="xs-assist-label">按数值大小升序，越小排在越前</p>
              </el-form-item>
              <el-form-item label="显示状态" prop="frozen">
                <el-switch v-model="poster.frozen" :active-text="poster.frozen ? '发布' : '隐藏'"></el-switch>
              </el-form-item>
            </el-form>
          </div>
          <div class="next-footer" style="margin-left:120px;">
            <el-button size="small" type="primary" @click="save" :loading="btn_loading">{{poster.type == 1 ? "保存并下一步" : "保存"}}</el-button>
            <el-button size="small" @click="back">返回</el-button>
          </div>
        </el-tab-pane>

        <el-tab-pane label="② 编辑详情" name="second" :disabled="next_disbaled" style="padding-bottom: 30px;" v-if="poster.type == 1">
          <vue-html5-editor ref="editor" :content="poster.content" :show-module-name="false" :height="500" @change="updateData"></vue-html5-editor>
          <div class="next-footer" style="margin-left:120px;margin-top: 10px;">
            <el-button size="small" type="primary" @click="saveDesc()" :loading="btn_desc_loading">保存</el-button>
            <el-button size="small" @click="activeName='first'">上一步</el-button>
          </div>
        </el-tab-pane>

      </el-tabs>
    </div>

    <!-- 上传图片 -->
    <upload-photo
      :limit="1"
      action = "change"
      :show.sync="showPhotoDialog"
      @confirm="confirmPhoto"
    ></upload-photo>

    <!-- 富文本所用 -->
    <upload-photo
      :limit="5"
      :limit_max="false"
      :show="isShowUpload"
      :current_len="editor_imgs.length"
      @confirm="confirmEditorImg"
      @update:show="switchEditorShow"
    ></upload-photo>

  </div>

</template>

<script>
  import { mapGetters, mapMutations } from 'vuex';
  import UploadPhoto from 'base/upload-photo/upload-photo.vue';
  import PosterApi from '@/api/energy.poster.js';

  export default {
    name: 'poster-list',
    components: {
      UploadPhoto
    },
    computed: {
      ...mapGetters([
        'isShowUpload',
        // ...
      ])
    },
    data() {
      return {
        editor_imgs: [],
        showPhotoDialog: false,

        btn_loading: false,
        btn_desc_loading: false,
        activeName: "first",
        formLabelWidth: '120px',
        poster: {frozen: true, isHot: false, sort: 100, content: ""},
        next_disbaled: true,
        rules:{
          title: [
            { required: true, message: '请输入标题', trigger: 'blur' },
          ],
          author: [
            { required: true, message: '作者不能为空', trigger: 'blur' },
          ],
          cid: [
            { required: true, message: '请选择分类', trigger: 'blur' },
          ],
        },

        cate_list: [],
        choose_cate_arr: []
      }
    },
    created() {
      if (this.$route.params.type) {
        this.poster.type = this.$route.params.type
      }
      if (this.$route.params.id) {
        this.poster.id = this.$route.params.id
        this.getPosterInfo()
      }
      this.getCateList()
    },
    methods: {

      getPosterInfo() {
        PosterApi.info(this.poster.id).then((res) => {
          this.next_disbaled = false
          this.poster = res.poster
          this.poster.content = this.poster.content ? this.poster.content : ""
          this.poster.frozen = Boolean(this.poster.frozen)
          this.getCateList()
        }, (err) => {
          this.$message.error(err.msg);
        })
      },

      save() {
        this.$refs.EditForm.validate((valid) => {
          if (valid) {
            this.btn_loading = true
            PosterApi.save(this.poster).then((res) => {
              this.btn_loading = false
              if (res.code === 0) {
                this.$message.success('保存成功！');
                this.poster.id = res.posterId
                if (this.poster.type == 1) {
                  this.activeName = 'second'
                  this.next_disbaled = false
                } else {
                  this.back()
                }
              } else {
                this.$message.error(res.msg);
              }
            }, (err) => {
              this.btn_loading = false
              this.$message.error(err.msg);
            })
          }
        });
      },
      saveDesc() {
        this.btn_desc_loading = true
        PosterApi.save({
          id: this.poster.id,
          content: this.poster.content
        }).then((res) => {
          this.btn_desc_loading = false
          if (res.code === 0) {
            this.$message.success('保存成功！');
            this.activeName = 'first'
            this.back()
          } else {
            this.$message.error(res.msg);
          }
        }, (err) => {
          this.btn_desc_loading = false
          this.$message.error(err.msg);
        })
      },
      // 确认图片
      confirmPhoto(pics) {
        this.poster.thumb = pics[0]
      },

      // 更新详情内容
      updateData(content) {
        this.poster.content = content
      },

      // 返回
      back(){
        if (this.poster.type == 1) {
          this.$router.push({path: `/energy/manager/poster`})
        } else {
          this.$router.push({path: `/energy/manager/video`})
        }
      },

      // 分类选择列表
      getCateList() {
        PosterApi.getCategoryChooseList(this.poster.type).then((res) => {
          this.getChooseList(res.list, 0)
        }, (err) => {
          this.$message.error(err.msg);
        })
      },
      getChooseList(list, hr){
        if (!list){
          return
        }
        if (hr == 0) this.cate_list=[]
        hr += 1
        list.forEach(item => {
          var tip = 20
          for (var i=2; i<=hr; i++){
            tip += 20;
          }
          this.cate_list.push({
            id: item.id,
            name: item.name,
            style: tip + 'px'
          })
          this.getChooseList(item.list, hr)
        })
      },

      ...mapMutations([
        'showUpload'
      ]),
      confirmEditorImg(imgs) {
        this.showUpload(false)
        console.log(imgs)
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        // 异步上传图片
        PosterApi.uploadPic(imgs).then((res) => {
          loading.close()
          if (res.picArr) {
            res.picArr.forEach(pic => {
              this.$refs.editor.execCommand("insertImage", pic)
            })
          }
        }, (err) => {
          loading.close()
          this.$message.error(err.msg);
        })
      },
      switchEditorShow(val) {
        this.showUpload(val)
      }
    }
  }
</script>

<style lang="scss">
  .goods-add-content {
    .form__button {
      font-size: 12px;
      color: #38f;
      margin-left: 5px;
    }
  }
</style>



