<template>
  <div class="poster-list">
    <common-template>
      <h1 class="content__title" slot="title">{{$route.meta.title}}</h1>

      <div class="content__main-operation" slot="header">
        <div class="operation-left">
          <el-button type="primary" size="small" @click="handleAdd">新增广告福利</el-button>
          <el-button size="small" plain @click="handleDelete(0)">批量删除</el-button>
        </div>
      </div>

      <div class="content__main-list" slot="list">
        <el-table
          v-loading = "!list"
          ref="multipleTable"
          @selection-change="handleSelectionChange"
          :data="list"
          tooltip-effect="dark"
          style="width: 100%">

          <el-table-column
            type="selection"
            width="55">
          </el-table-column>

          <el-table-column
            label="广告信息"
            align="left">
            <template slot-scope="scope">
              <div class="info-item">
                <i class="item__image" :style="{backgroundImage: 'url('+scope.row.pic+')'}" v-if="scope.row.pic"></i>
                <div class="item__info">
                  <p class="item__info-main" style="font-weight: 500;">{{scope.row.title}} <el-tag size="mini" :type="scope.row.type==1?'warning':'danger'">{{scope.row.type==1?"自营":"微信"}}</el-tag></p>
                  <p class="item__info-sub">{{scope.row.linkman}}</p>
                  <p class="item__info-sub">{{scope.row.linkTel}}</p>
                </div>
              </div>
            </template>
          </el-table-column>

          <el-table-column
            label="广告跳转"
            align="left">
            <template slot-scope="scope">
              <div v-if="scope.row.type == 1">
                <p>跳转到小程序</p>
                <p>{{scope.row.appid}}</p>
                <p>{{scope.row.appPage}}</p>
              </div>
              <div v-if="scope.row.type == 2">
                <p>对接微信广告</p>
                <p>{{scope.row.wxadKey}}</p>
              </div>
            </template>
          </el-table-column>

          <el-table-column
            width="100"
            label="可领取能量"
            align="center"
            prop="energy">
          </el-table-column>

          <el-table-column
            width="80"
            label="排序"
            align="center"
            prop="sort">
          </el-table-column>

          <el-table-column
            width="160"
            label="发布状态"
            align="center">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.frozen"
                :active-text="scope.row.frozen ? '已发布' : '隐藏中'"
                @change = "changeStatus(scope.row.id, scope.row.frozen)">
              </el-switch>
            </template>
          </el-table-column>

          <el-table-column
            width="160"
            label="创建时间"
            align="center"
            prop="createTime">
          </el-table-column>

          <el-table-column
            width="160"
            label="操作"
            align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index)">编辑</el-button>
              <el-button
                type="danger"
                size="mini"
                @click="handleDelete(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>

        </el-table>

        <div style="text-align: center; margin-top: 60px;" v-if="list && list.length>0">
          <el-pagination
            :page-sizes="[10, 20, 30, 40, 50]"
            :page-size="page.pageSize"
            :current-page="page.pageNo"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.totalRows"
            @size-change = "changePageSize"
            @current-change = "changePageNo">
          </el-pagination>
        </div>

      </div>
    </common-template>

    <!-- 添加、编辑 -->
    <el-dialog title="广告福利" :visible.sync="editVisible">
      <el-form :model="advert" ref="EditForm" :rules="rules" label-width="130px">

        <el-form-item label="图片" prop="pic">
          <ul class="app-image-list">
            <li class="image-item" v-if="advert.pic" @click="showPhotoDialog=true">
              <img :src="advert.pic" role="presentation" alt="" width="50">
            </li>
            <li @click="showPhotoDialog=true" v-else class="el-upload el-upload--advert-card" style="width: 80px;height: 80px;line-height: 1;">
              <i class="el-icon-plus" style="font-size: 40px;margin-top: 20px;"></i>
            </li>
          </ul>
        </el-form-item>

        <el-form-item label="客户名称" prop="linkman">
          <el-input v-model.trim="advert.linkman"></el-input>
        </el-form-item>
        <el-form-item label="联系方式" prop="linkTel">
          <el-input v-model.trim="advert.linkTel"></el-input>
        </el-form-item>
        <el-form-item label="广告标题" prop="title">
          <el-input v-model.trim="advert.title"></el-input>
          <p class="xs-assist-label">可以用于备注说明</p>
        </el-form-item>

        <el-form-item label="广告类型" prop="frozen">
          <el-radio-group v-model="advert.type">
            <el-radio :label="1"><span style="font-weight: normal">自营</span></el-radio>
            <el-radio :label="2"><span style="font-weight: normal">微信</span></el-radio>
          </el-radio-group>
        </el-form-item>

        <template v-if="advert.type == 1">
          <el-form-item label="小程序appid" prop="appid">
            <el-input v-model.trim="advert.appid"></el-input>
          </el-form-item>
          <el-form-item label="小程序页面" prop="appPage">
            <el-input v-model.trim="advert.appPage"></el-input>
          </el-form-item>
        </template>

        <template v-if="advert.type == 2">
          <el-form-item label="微信广告主key" prop="wxadKey">
            <el-input v-model.trim="advert.wxadKey"></el-input>
          </el-form-item>
        </template>

        <el-form-item label="可领取的能量值" prop="energy">
          <el-input size="small" v-model="advert.energy"></el-input>
          <p class="xs-assist-label">同个广告，每个用户只能领取一次？</p>
        </el-form-item>

        <el-form-item label="排序字段" prop="sort">
          <el-input size="small" v-model="advert.sort"></el-input>
          <p class="xs-assist-label">按数值大小升序，越小排在越前</p>
        </el-form-item>

        <el-form-item label="显示状态" prop="frozen">
          <el-switch v-model="advert.frozen" :active-text="advert.frozen ? '显示中' : '已冻结'"></el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="save()" :loading="loading" size="mini">确 定</el-button>
      </div>
    </el-dialog>

    <upload-photo
      :limit="1"
      action="change"
      :show.sync="showPhotoDialog"
      @confirm="confirmPhoto"
    ></upload-photo>

  </div>

</template>

<script>
  import CommonTemplate from '@/components/common-template'
  import UploadPhoto from 'base/upload-photo/upload-photo.vue';
  import AdvertApi from '@/api/advert.js'

  export default {
    name: 'advert-list',
    components: { CommonTemplate, UploadPhoto },
    data() {
      var checkAppid = (rule, value, callback) => {
        if (this.advert.type == 1 && !value) {
          callback(new Error('请填写小程序appid'));
        } else {
          callback();
        }
      };
      var checkWxadKey = (rule, value, callback) => {
        if (this.advert.type == 2 && !value) {
          callback(new Error('请填写微信广告主key'));
        } else {
          callback();
        }
      };
      return {
        showPhotoDialog: false,
        loading: false,
        editVisible: false,
        list: null,
        advert: {position: 2, sort: 100, frozen: true},
        req: {position: 2},
        page: {pageNo:1, pageSize: 20},
        choose_id_arr: [],
        rules:{
          linkman: [
            { required: true, message: '请填写客户名称', trigger: 'blur' },
          ],
          linkTel: [
            { required: true, message: '请填写客户联系方式', trigger: 'blur' },
          ],
          title: [
            { required: true, message: '请填写广告标题', trigger: 'blur' },
          ],
          energy: [
            { required: true, message: '请填写可领取的能量', trigger: 'blur' },
          ],
          appid: [
            { validator: checkAppid, trigger: 'blur' },
          ],
          wxadKey: [
            { validator: checkWxadKey, trigger: 'blur' },
          ],
        }
      }
    },
    created() {
      this.getList()
    },
    methods: {
      getList() {
        this.req.p = this.page.pageNo
        this.req.s = this.page.pageSize
        AdvertApi.getList(this.req).then((res) => {
          this.list = res.list ? res.list : []
          this.list.forEach(item => {
            item.frozen = Boolean(item.frozen)
          })
          this.page = res.page
        }, (err) => {
          this.$message.error(err.msg);
        })
      },
      reload() {
        this.page.pageNo = 1
        this.getList()
      },
      changePageSize(value){ // 修改每页条数size
        this.page.pageNo = 1
        this.page.pageSize = value
        this.list = null
        this.getList()
      },
      changePageNo(value){ // 修改当前页currentPage
        this.page.pageNo = value
        this.list = null
        this.getList()
      },

      handleSelectionChange(val) {
        this.choose_id_arr = val
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
        this.choose_id_arr = rows
      },

      handleAdd(){
        this.editVisible = true
        this.advert = { position: 2, sort: 100, frozen: true, type: 1}
      },
      handleEdit(idx){
        this.editVisible = true
        this.advert = this.list[idx]
      },

      save() {
        this.$refs.EditForm.validate((valid) => {
          if (valid) {
            this.loading = true
            AdvertApi.save(this.advert).then((res) => {
              this.loading = false
              if (res.code === 0) {
                this.editVisible = false
                this.$message.success('保存成功！');
                this.reload()
              } else {
                this.$message.error(res.msg);
              }
            }, (err) => {
              this.loading = false
              this.$message.error(err.msg);
            })
          }
        });
      },

      // 上下架
      changeStatus(id, flag) {
        AdvertApi.batchPublish([id], !flag?0:1).then((res) => {
          this.$message.success('操作成功');
          this.reload()
        }, (err) => {
          this.$message.error(err.msg);
        })
      },

      // 批量删除
      handleDelete(id) {
        let ids = [id]
        if (id == 0) { // 批量删除
          if (this.choose_id_arr.length === 0) {
            this.$message.error("请至少选择一项！");
            return
          }
          ids = this.choose_id_arr.map(item => item.id)
        }

        this.$confirm('删除后无法恢复，是否确认？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          AdvertApi.batchDelete(ids).then((res) => {
            this.$message.success('操作成功');
            this.reload()
          }, (err) => {
            this.$message.error(err.msg);
          })
        })
      },

      //  图片添加
      confirmPhoto(pics) {
        this.advert.pic = pics[0]
      },

    }
  }
</script>

<style lang="scss">

</style>



