<template>
  <div class="common-add-content goods-add-content">
    <h1 class="content__title">
      <router-link to="/store/manager/goods">商品列表 </router-link> / {{$route.meta.title}}
    </h1>

    <div class="content__main" style="margin-top: 20px;">
      <el-tabs v-model="activeName">
        <el-tab-pane label="① 填写基本信息" class="main-first" name="first" style="padding-bottom: 30px;">
          <div class="section">
            <h3 class="section-title">基本信息</h3>
            <div class="section-content">
              <el-form :rules="rules" ref="EditForm" :model="goods" :label-width="formLabelWidth" style="width: 600px;">
                <el-form-item label="缩略图" prop="thumb">
                  <ul class="app-image-list">
                    <li class="image-item" v-if="goods.thumb" @click="showPhotoDialog=true">
                      <img :src="goods.thumb" role="presentation" alt="" width="80">
                    </li>
                    <li @click="showPhotoDialog=true" v-else class="el-upload el-upload--picture-card" style="width: 80px;height: 80px;line-height: 1;">
                      <i class="el-icon-plus" style="font-size: 40px;margin-top: 20px;"></i>
                    </li>
                  </ul>
                </el-form-item>

                <el-form-item label="商品名称" prop="name">
                  <el-input size="small" v-model="goods.name"></el-input>
                </el-form-item>

                <el-form-item label="排序字段" prop="sort">
                  <el-input size="small" v-model="goods.sort"></el-input>
                  <p class="xs-assist-label">按数值大小升序，越小排在越前</p>
                </el-form-item>
                <el-form-item label="显示状态" prop="frozen">
                  <el-switch v-model="goods.frozen" :active-text="goods.frozen ? '上架中' : '已下架'"></el-switch>
                </el-form-item>
              </el-form>
            </div>
          </div>
          <div class="section">
            <h3 class="section-title">规格/价格/库存</h3>
            <div class="section-content">
              <el-form :rules="rules" ref="EditForm" :model="goods" :label-width="formLabelWidth">
                <el-form-item label="商品规格" v-if="showAttrBtn || goods.attrs">
                  <sku
                    :showAttrBtn="showAttrBtn"
                    :sku_group_list_prop="initSkuList"
                    @update="updateSkuTable"
                  ></sku>
                </el-form-item>

                <el-form-item label="规格明细" v-show="computed_sku_group_list && computed_sku_group_list.length > 0">
                  <sku-table>
                    <tr slot="head">
                      <th v-for="item in computed_sku_group_list" v-if="item.attrs.length > 0">
                        {{item.name}}
                      </th>
                      <th class="th-saleprice">消费能量</th>
                      <th class="th-stock">库存</th>
                      <th class="th-operation">操作</th>
                    </tr>
                    <tr v-for="(row_item, row) in skuList" slot="foot">
                      <template v-for="(item, index) in row_item.attrs">
                        <td v-if="needShow(row, index)" :rowspan="getRow(row, index)" class="middle-td">{{item}}</td>
                      </template>
                      <td>
                        <el-input size="small" v-model="row_item.energy" :disabled="!showAttrBtn"></el-input>
                      </td>
                      <td>
                        <el-input size="small" @change="stockKeyup(row)" v-model="row_item.stock"></el-input>
                      </td>
                      <td>
                        <span v-if="row_item.frozen === 0" style="color:red;">禁用中</span>
                        <span v-if="row_item.frozen === 1" style="color: green">已启用</span>
                        <el-button type="text" class="form__button" @click="row_item.frozen = !row_item.frozen">{{row_item.frozen==1?"禁用":"启用"}}</el-button>
                      </td>
                    </tr>
                    <tr slot="foot">
                      <td colspan="8">
                        <div class="setting-wrap">
                          <span>批量设置:</span>
                          <el-radio-group v-model="batchInfo.type" size="mini" style="margin-left: 20px">
                            <el-radio-button label="1" >能量</el-radio-button>
                            <el-radio-button label="3">库存</el-radio-button>
                          </el-radio-group>
                          <el-input size="mini" class="xs-input" v-model="batchInfo.number"></el-input>
                          <el-button size="mini" type="primary" class="xs-button" @click.stop="batchSetNumber">确认</el-button>
                          <div class="setting__total-wrap">
                            <span>总库存</span>
                            <el-input size="mini" :disabled="true" :value="goods.stock" class="xs-input"></el-input>
                          </div>
                        </div>
                      </td>
                    </tr>
                  </sku-table>
                </el-form-item>

                <div v-show="!computed_sku_group_list || computed_sku_group_list.length === 0">
                  <el-form-item label="能量" prop="energy" style="width: 300px;">
                    <el-input class="xs-input" v-model="goods.energy" size="mini" :disabled="!showAttrBtn">
                      <!--<template slot="prepend">¥</template>-->
                    </el-input>
                  </el-form-item>
                  <el-form-item label="库存" prop="stock"  style="width: 300px;">
                    <el-input class="xs-input" size="mini" v-model="goods.stock">
                    </el-input>
                  </el-form-item>
                </div>

              </el-form>
            </div>
          </div>

          <div class="next-footer" style="margin-left:120px;">
            <el-button size="small" type="primary" @click="save" :loading="btn_loading">{{goods.type == 1 ? "保存并下一步" : "保存"}}</el-button>
            <el-button size="small" @click="back">返回</el-button>
          </div>
        </el-tab-pane>

        <el-tab-pane label="② 编辑详情" name="second" :disabled="next_disbaled" style="padding-bottom: 30px;" v-if="goods.type == 1">
          <vue-html5-editor ref="editor" :content="goods.content" :show-module-name="false" :height="500" @change="updateData"></vue-html5-editor>
          <div class="next-footer" style="margin-left:120px;margin-top: 10px;">
            <el-button size="small" type="primary" @click="saveDesc()" :loading="btn_desc_loading">保存</el-button>
            <el-button size="small" @click="activeName='first'">上一步</el-button>
          </div>
        </el-tab-pane>

      </el-tabs>
    </div>

    <!-- 上传图片 -->
    <upload-photo
      :limit="1"
      action = "change"
      :show.sync="showPhotoDialog"
      @confirm="confirmPhoto"
    ></upload-photo>

    <!-- 富文本所用 -->
    <upload-photo
      :limit="5"
      :limit_max="false"
      :show="isShowUpload"
      :current_len="editor_imgs.length"
      @confirm="confirmEditorImg"
      @update:show="switchEditorShow"
    ></upload-photo>

  </div>

</template>

<script>
  import { mapGetters, mapMutations } from 'vuex';
  import UploadPhoto from 'base/upload-photo/upload-photo.vue';
  import SkuTable from 'base/xs-sku/xs-sku-table.vue'
  import Sku from 'base/xs-sku/xs-sku.vue'
  import GoodsApi from '@/api/store.goods.js';

  export default {
    name: 'goods-list',
    components: {
      UploadPhoto,
      Sku,
      SkuTable
    },
    computed: {
      ...mapGetters([
        'isShowUpload',
        // ...
      ])
    },
    data() {
      return {
        showAttrBtn: true,
        initSkuList: [],
        skuListPool: [],
        computed_sku_group_list: [],
        skuList: [],
        batchInfo: {type: 1, number: 0},

        editor_imgs: [],
        showPhotoDialog: false,

        btn_loading: false,
        btn_desc_loading: false,
        activeName: "first",
        formLabelWidth: '120px',
        goods: {frozen: true, sort: 100, content: "", price: 0, type:1 },
        next_disbaled: true,
        rules:{
          thumb: [
            { required: true, message: '请选择图片', trigger: 'blur' },
          ],
          name: [
            { required: true, message: '请输入商品名称', trigger: 'blur' },
          ],
          energy: [
            { required: true, message: '请输入能量', trigger: 'blur' },
          ],
          stock: [
            { required: true, message: '请输入商品库存', trigger: 'blur' },
          ],
        },
      }
    },
    created() {
      this.goods.type = 1
      if (this.$route.params.id) {
        this.goods.id = this.$route.params.id
        this.getGoodsInfo()
      }
    },
    methods: {
      // 商品信息
      getGoodsInfo() {
        GoodsApi.info(this.goods.id).then((res) => {
          this.next_disbaled = false
          this.goods = res.goods
          this.goods.content = this.goods.content ? this.goods.content : ""
          this.goods.frozen = Boolean(this.goods.frozen)

          // 商品属性
          if (this.goods.attrs) {
            this.goods.attrs = JSON.parse(this.goods.attrs)
            let index = 0
            this.goods.attrs.forEach(item => {
              let attr_temps = []
              item.attrs.forEach(item2 => {
                attr_temps.push(item2.name)
              })
              item.attr_temps = attr_temps
              item.show_drop_down = false
              item.show_ui_popover = false
              item.id = index ++
            })

            if (this.goods.skuList) {
              this.goods.skuList.forEach(elm => {
                elm.attrs = JSON.parse(elm.attrs)
              })
              this.skuListPool = this.skuListPool.concat(this.goods.skuList);
              let attrs = this.goods.attrs.slice(0);
              this.initSkuList = attrs;
            }
          }
        }, (err) => {
          this.$message.error(err.msg);
        })
      },

      // sku表格更新
      updateSkuTable({need_clear, sku_group_list}) {
        var skuAttrsList = [];
        this.skuList = [];
        this.computed_sku_group_list = [];
        if (need_clear) this.skuListPool = [];
        for (let sku_group of sku_group_list) {
          if (sku_group.attr_temps && sku_group.attr_temps.length > 0) {
            skuAttrsList.push(sku_group.attr_temps);
            this.computed_sku_group_list.push(sku_group)
          }
        }

        var group = function(doubleArrays){
          var len = doubleArrays.length;
          if (len >= 2) {
            var arr1 = doubleArrays[0];
            var arr2 = doubleArrays[1];
            var len1 = doubleArrays[0].length;
            var len2 = doubleArrays[1].length;

            var newlen = len1 * len2;
            var temp = new Array(newlen);
            var index = 0;
            for (var i = 0; i < len1; i++) {
              for (var j = 0; j < len2; j++) {
                temp[index] = (arr1[i] ? arr1[i] : arr1[i]) + "," + arr2[j];
                index++;
              }
            }
            var newArray = new Array(len - 1);
            newArray[0] = temp;
            if (len > 2) {
              var _count = 1;
              for (var i = 2; i < len; i++) {
                newArray[_count] = doubleArrays[i];
                _count++;
              }
            }
            return group(newArray);
          }
          else {
            return doubleArrays[0];
          }
        }

        var group_attr = group(skuAttrsList);
        if (!group_attr) return;
        for (let group_attr_item of group_attr) {
          let temp_group = group_attr_item.split(',');

          var _sku = this.skuListPool.find((elm) => elm.attrs.join(',') === group_attr_item)
          if (!_sku) {
            _sku = {
              "attrs" : temp_group,
              "stock" : 0,
              "energy" : 0,
              "price" : 0,
              "frozen" : 1,
            }
          }
          this.skuListPool.push(_sku);
          this.skuList.push(_sku)
        }
      },
      getRow(row, index) {
        if (this.computed_sku_group_list.length === 0) return 1;
        var rows = 1;
        for (var i = index + 1; i < this.computed_sku_group_list.length; i ++) {
          rows = rows * (this.computed_sku_group_list[i].attrs.length === 0 ? 1 :this.computed_sku_group_list[i].attrs.length ) ;
        }
        return rows;
      },
      needShow(row, index) {
        var rows = this.getRow(row, index);
        return row % rows === 0;
      },
      // 计算库存
      stockKeyup: function(idx){
        let stock = 0
        this.skuList.forEach(item => {
          if (item.stock === undefined || item.stock === null || item.stock === '') item.stock = 0;
          stock += parseInt(item.stock)
        })
        this.goods.stock = stock
      },
      // 批量设置
      batchSetNumber(){
        if (this.batchInfo.type == 1){ // 能量
          this.skuList.forEach(item => {
            item.energy = parseFloat(this.batchInfo.number)
          })
        } else if (this.batchInfo.type == 2){ // 价格
          this.skuList.forEach(item => {
            item.price = parseFloat(this.batchInfo.number)
          })
        } else if (this.batchInfo.type == 3){ // 库存
          let stock = 0
          this.skuList.forEach(item => {
            item.stock = parseInt(this.batchInfo.number)
            stock += item.stock
          })
          this.goods.stock = stock
        }
      },

      save() {
        this.$refs.EditForm.validate((valid) => {
          if (valid) {
            if (this.skuList && this.skuList.length > 0) {
              let energys = this.skuList.map(item => item.energy)
              this.goods.energy = Math.min.apply(null, energys)
            }else {
              if (!this.goods.energy) {
                this.$message.error("请输入商品换购所需能量");
                return
              }
            }
            this.btn_loading = true
            this.goods.attrs = this.computed_sku_group_list
            this.goods.skuList = this.skuList
            GoodsApi.save(this.goods).then((res) => {
              this.btn_loading = false
              if (res.code === 0) {
                this.$message.success('保存成功！');
                this.goods.id = res.goodsId
                this.activeName = 'second'
                this.next_disbaled = false
              } else {
                this.$message.error(res.msg);
              }
            }, (err) => {
              this.btn_loading = false
              this.$message.error(err.msg);
            })
          }
        });
      },
      saveDesc() {
        this.btn_desc_loading = true
        GoodsApi.save({
          id: this.goods.id,
          content: this.goods.content
        }).then((res) => {
          this.btn_desc_loading = false
          if (res.code === 0) {
            this.$message.success('保存成功！');
            this.activeName = 'first'
            this.back()
          } else {
            this.$message.error(res.msg);
          }
        }, (err) => {
          this.btn_desc_loading = false
          this.$message.error(err.msg);
        })
      },
      // 确认图片
      confirmPhoto(pics) {
        this.goods.thumb = pics[0]
      },

      // 更新详情内容
      updateData(content) {
        this.goods.content = content
      },

      // 返回
      back(){
        this.$router.push({path: `/store/manager/goods`})
      },

      ...mapMutations([
        'showUpload'
      ]),
      confirmEditorImg(imgs) {
        this.showUpload(false)
        console.log(imgs)
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        // 异步上传图片
        GoodsApi.uploadPic(imgs).then((res) => {
          loading.close()
          if (res.picArr) {
            res.picArr.forEach(pic => {
              this.$refs.editor.execCommand("insertImage", pic)
            })
          }
        }, (err) => {
          loading.close()
          this.$message.error(err.msg);
        })
      },
      switchEditorShow(val) {
        this.showUpload(val)
      }
    }
  }
</script>

<style lang="scss">
  .goods-add-content {
    .form__button {
      font-size: 12px;
      color: #38f;
      margin-left: 5px;
    }
  }
</style>



