<template>
  <div class="member-list">
    <common-template>
      <h1 class="content__title" slot="title">{{$route.meta.title}}</h1>

      <div slot="header" class="content__main-operation">
        <div class="operation-left">
          <el-button type="primary" size="small" @click="handleAdd">新增常见问题</el-button>
          <el-button size="small" plain @click="handleDelete(0)">批量删除</el-button>
        </div>
      </div>

      <div class="content__main-list" slot="list">
        <el-table
          v-loading = "!list"
          @selection-change="handleSelectionChange"
          ref="multipleTable"
          :data="list"
          tooltip-effect="dark"
          style="width: 100%">

          <el-table-column
            type="selection"
            width="55">
          </el-table-column>

          <el-table-column
            label="问题"
            align="left"
            prop="title">
          </el-table-column>
          <el-table-column
            label="回答"
            align="left"
            prop="brief">
          </el-table-column>

          <el-table-column
            label="排序"
            width="50"
            align="center"
            prop="sort">
          </el-table-column>
          <el-table-column
            width="150"
            label="显示状态"
            align="center">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.frozen"
                :active-text="scope.row.frozen ? '显示中' : '已冻结'"
                @change = "changeStatus(scope.row.id, scope.row.frozen)">
              </el-switch>
            </template>
          </el-table-column>

          <el-table-column
            width="160"
            label="创建时间"
            align="center"
            prop="createTime">
          </el-table-column>

          <el-table-column
            label="操作"
            width="150"
            align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index)">编辑</el-button>
              <el-button
                type="danger"
                size="mini"
                @click="handleDelete(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>

        <div style="text-align: center; margin-top: 60px;" v-if="list && list.length>0">
          <el-pagination
            :page-sizes="[10, 20, 30, 40, 50]"
            :page-size="page.pageSize"
            :current-page="page.pageNo"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.totalRows"
            @size-change = "changePageSize"
            @current-change = "changePageNo">
          </el-pagination>
        </div>

      </div>
    </common-template>

    <!-- 添加、编辑 -->
    <el-dialog title="常见问题" :visible.sync="editVisible">
      <el-form :model="docs" ref="EditForm" :rules="rules" label-width="80px">
        <el-form-item label="问题" prop="title">
          <el-input v-model.trim="docs.title" auto-complete="off" placeholder="标题"></el-input>
        </el-form-item>
        <el-form-item label="回答" prop="brief">
          <el-input v-model.trim="docs.brief" auto-complete="off" placeholder="说明"></el-input>
        </el-form-item>

        <el-form-item label="排序字段" prop="sort">
          <el-input size="small" v-model="docs.sort"></el-input>
          <p class="xs-assist-label">按数值大小升序，越小排在越前</p>
        </el-form-item>

        <el-form-item label="显示状态" prop="frozen">
          <el-switch v-model="docs.frozen" :active-text="docs.frozen ? '显示中' : '已冻结'"></el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false" size="mini">取 消</el-button>
        <el-button type="primary" @click="save()" :loading="loading" size="mini">确 定</el-button>
      </div>
    </el-dialog>

  </div>

</template>

<script>
  import CommonTemplate from '@/components/common-template'
  import DocApi from '../../api/docs';

  export default {
    name: 'docs-list',
    components: { CommonTemplate },
    data() {
      return {
        loading: false,
        editVisible: false,
        docs: { frozen: true, sort: 100},
        list: null,
        page: {pageNo:1, pageSize: 20},
        choose_id_arr: [],
        rules:{
          title: [
            { required: true, message: '请输入标题', trigger: 'blur' },
          ],
          brief: [
            { required: true, message: '请输入描述', trigger: 'blur' },
          ],
          sort: [
            { required: true, message: '请输入排序', trigger: 'blur' },
          ],
        }
      }
    },
    created() {
      this.getList()
    },
    methods: {
      getList() {
        let req = {
          p: this.page.pageNo,
          s: this.page.pageSize,
        }
        DocApi.getQuestionList(req).then((res) => {
          this.list = res.list
          this.list.forEach(item => {
            item.frozen = Boolean(item.frozen)
          })
          this.page = res.page
        }, (err) => {
          this.$message.error(err.msg);
        })
      },

      save() {
        this.$refs.EditForm.validate((valid) => {
          if (valid) {
            this.loading = true
            DocApi.saveQuestion(this.docs).then((res) => {
              this.loading = false
              if (res.code === 0) {
                this.editVisible = false
                this.$message.success('保存成功！');
                this.reload()
              } else {
                this.$message.error(res.msg);
              }
            }, (err) => {
              this.loading = false
              this.$message.error(err.msg);
            })
          }
        });
      },

      reload() {
        this.list = null
        this.page.pageNo = 1
        this.getList()
      },

      handleSelectionChange(val) {
        this.choose_id_arr = val
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
        this.choose_id_arr = rows
      },

      changePageSize(value){ // 修改每页条数size
        this.page.pageNo = 1
        this.page.pageSize = value
        this.list = null
        this.getList()
      },
      changePageNo(value){ // 修改当前页currentPage
        this.page.pageNo = value
        this.list = null
        this.getList()
      },

      handleAdd(){
        this.editVisible = true
        this.docs = { frozen: true, sort: 100 }
      },
      handleEdit(idx){
        this.editVisible = true
        this.docs = this.list[idx]
      },

      // 上下架
      changeStatus(id, flag) {
        DocApi.batchPublish([id], !flag?0:1).then((res) => {
          this.$message.success('操作成功');
          this.reload()
        }, (err) => {
          this.$message.error(err.msg);
        })
      },

      // 批量删除
      handleDelete(id) {
        let ids = [id]
        if (id == 0) { // 批量删除
          if (this.choose_id_arr.length === 0) {
            this.$message.error("请至少选择一项！");
            return
          }
          let ids = this.choose_id_arr.map(item => item.id)
        }

        this.$confirm('删除后无法恢复，是否确认？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          DocApi.batchDelete(ids).then((res) => {
            this.$message.success('操作成功');
            this.reload()
          }, (err) => {
            this.$message.error(err.msg);
          })
        }).catch(() => {
        });
      }
    }
  }
</script>

<style lang="scss">
  .member-list {

  }
</style>



