<template>
  <div>
    <common-template>
      <h1 class="content__title" slot="title">{{$route.meta.title}}</h1>

      <div class="content__main-operation" slot="header">
        <div class="operation-left">
          <el-button type="primary" size="small" @click="add">新建标签</el-button>
        </div>
        <div class="operation-right">
          <el-input size="small" placeholder="分类名称" v-model="filterText">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </div>
      </div>

      <div slot="list">
        <el-tree
          style="width: 500px"
          v-loading="!list"
          class="filter-tree"
          :data="list"
          node-key="id"
          default-expand-all
          :expand-on-click-node="false"
          :render-content="renderContent"
          :filter-node-method="filterNode"
          ref="trees">
        </el-tree>
      </div>
    </common-template>

    <!-- 添加、编辑 -->
    <el-dialog
      title="标签信息"
      :visible.sync="editVisible"
      width="40%">
      <el-form ref="form" :model="tag" :label-width="formLabelWidth">
        <el-form-item label="标签名称" prop="name" required>
          <el-input size="small" v-model="tag.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="editVisible = false">取 消</el-button>
        <el-button size="small" type="primary" @click="save">确 定</el-button>
      </span>
    </el-dialog>

  </div>

</template>

<script>
  import CommonTemplate from '@/components/common-template'
  import TagApi from '@/api/energy.tag.js'

  export default {
    name: 'tag-list',
    components: {CommonTemplate},
    watch: {
      filterText(val) {
        this.$refs.trees.filter(val);
      }
    },
    data () {
      return {
        editVisible: false,
        filterText: '',
        formLabelWidth: '100px',
        tag: {name: ""},
        list: null,
        choose_id_arr: [],
      }
    },
    created() {
      this.getList()
    },
    methods: {
      renderContent(h, { node, data, store }) {
        return (
          <div class="custom-tree-node">
            <span>{node.data.name}</span>
            <div>
              <el-button size="mini" type="text" on-click={ () => this.edit(node.data.idx) }>修改</el-button>
              <el-button size="mini" type="text" on-click={ () => this.deleteOne(node.data.idx) }>删除</el-button>
            </div>
          </div>);
      },

      filterNode(value, data) {
        if (!value) return true;
        return data.name.indexOf(value) !== -1;
      },

      getList(){
        this.list = null
        TagApi.getList().then((res) => {
          this.list = res.list
          this.list = this.list.map((item, idx) => {
            return  {
              idx: idx,
              id: idx + 1,
              name: item
            }
          })
        }, (err) => {
          this.list = []
          this.$message.error(err.msg);
        })
      },

      // 保存
      save() {
        if (this.tag.name==null || this.tag.name==""){
          this.$message.error("请输入名称");
          return
        }
        let list = this.list.map(item => item.name)
        if (!this.tag.id) {
          list.push(this.tag.name)
        }
        TagApi.save(list).then((res) => {
          this.$message.success('保存成功');
          this.editVisible = false
          this.tag = {}
          this.getList()
        }, (err) => {
          this.$message.error(err.msg);
        })
      },
      add() {
        this.editVisible = true
        this.tag = {name: ""}
      },
      edit(idx) {
        this.editVisible = true
        this.tag = this.list[idx]
      },

      // 批量删除
      deleteOne(idx) {
        this.$confirm('删除后无法恢复，是否确认？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.list.splice(idx, 1)
          let list = this.list.map(item => item.name)
          TagApi.save(list).then((res) => {
            this.$message.success('保存成功');
            this.editVisible = false
            this.tag = {}
            this.getList()
          }, (err) => {
            this.$message.error(err.msg);
          })
        })
      },


    }
  }
</script>

<style lang="scss">
  @import "~common/style/adapter.config";
  .custom-tree-node {
  @include flex($al:center);
    width: 100%;

  > span {
    @include flex-full();
      margin-left: 10px;
    }
  }
</style>



