<template>
  <div class="common-add-content goods-add-content">
    <h1 class="content__title">
      <router-link to="/store/manager/cent">商品列表 </router-link> / {{$route.meta.title}}
    </h1>

    <div class="content__main" style="margin-top: 20px;">
      <el-tabs v-model="activeName">
        <el-tab-pane label="① 填写基本信息" class="main-first" name="first" style="padding-bottom: 30px;">
          <div class="section">
            <h3 class="section-title">基本信息</h3>
            <div class="section-content">
              <el-form :rules="rules" ref="EditForm" :model="goods" :label-width="formLabelWidth" style="width: 600px;">
                <el-form-item label="缩略图" prop="thumb">
                  <ul class="app-image-list">
                    <li class="image-item" v-if="goods.thumb" @click="showPhotoDialog=true">
                      <img :src="goods.thumb" role="presentation" alt="" width="80">
                    </li>
                    <li @click="showPhotoDialog=true" v-else class="el-upload el-upload--picture-card" style="width: 80px;height: 80px;line-height: 1;">
                      <i class="el-icon-plus" style="font-size: 40px;margin-top: 20px;"></i>
                    </li>
                  </ul>
                </el-form-item>

                <el-form-item label="商品名称" prop="name">
                  <el-input size="small" v-model="goods.name"></el-input>
                </el-form-item>
                <el-form-item label="换购能量" prop="energy" style="width: 300px;">
                  <el-input class="xs-input" v-model="goods.energy" size="mini">
                  </el-input>
                </el-form-item>
                <el-form-item label="库存" prop="stock"  style="width: 300px;">
                  <el-input class="xs-input" size="mini" v-model="goods.stock">
                  </el-input>
                </el-form-item>

                <el-form-item label="初始销量" prop="salesNum">
                  <el-input style="width: 160px" size="small" v-model="goods.salesNum"></el-input>
                  <p class="xs-assist-label">设置后商品详情中会显示销量，销量值=初始销量+实际销量</p>
                </el-form-item>
                <el-form-item label="划线价" prop="marketPrice">
                  <el-input style="width: 160px" size="small" v-model="goods.marketPrice"></el-input>
                  <p class="xs-assist-label">设置后商品列表会展示划线价，用于营造优惠感</p>
                </el-form-item>

                <el-form-item label="排序字段" prop="sort">
                  <el-input size="small" v-model="goods.sort"></el-input>
                  <p class="xs-assist-label">按数值大小升序，越小排在越前</p>
                </el-form-item>
                <el-form-item label="显示状态" prop="frozen">
                  <el-switch v-model="goods.frozen" :active-text="goods.frozen ? '上架中' : '已下架'"></el-switch>
                </el-form-item>
              </el-form>
            </div>
          </div>

          <div class="next-footer" style="margin-left:120px;">
            <el-button size="small" type="primary" @click="save" :loading="btn_loading">{{goods.type == 1 ? "保存并下一步" : "保存"}}</el-button>
            <el-button size="small" @click="back">返回</el-button>
          </div>
        </el-tab-pane>

        <el-tab-pane label="② 编辑详情" name="second" :disabled="next_disbaled" style="padding-bottom: 30px;">
          <vue-html5-editor ref="editor" :content="goods.content" :show-module-name="false" :height="500" @change="updateData"></vue-html5-editor>
          <div class="next-footer" style="margin-left:120px;margin-top: 10px;">
            <el-button size="small" type="primary" @click="saveDesc()" :loading="btn_desc_loading">保存</el-button>
            <el-button size="small" @click="activeName='first'">上一步</el-button>
          </div>
        </el-tab-pane>

      </el-tabs>
    </div>

    <!-- 上传图片 -->
    <upload-photo
      :limit="1"
      action = "change"
      :show.sync="showPhotoDialog"
      @confirm="confirmPhoto"
    ></upload-photo>

    <!-- 富文本所用 -->
    <upload-photo
      :limit="5"
      :limit_max="false"
      :show="isShowUpload"
      :current_len="editor_imgs.length"
      @confirm="confirmEditorImg"
      @update:show="switchEditorShow"
    ></upload-photo>

  </div>

</template>

<script>
  import { mapGetters, mapMutations } from 'vuex';
  import UploadPhoto from 'base/upload-photo/upload-photo.vue';
  import GoodsApi from '@/api/store.goods.js';

  export default {
    name: 'goods-list',
    components: {
      UploadPhoto,
    },
    computed: {
      ...mapGetters([
        'isShowUpload',
        // ...
      ])
    },
    data() {
      return {

        editor_imgs: [],
        showPhotoDialog: false,

        btn_loading: false,
        btn_desc_loading: false,
        activeName: "first",
        formLabelWidth: '120px',
        goods: {frozen: true, sort: 100, content: "", price: 0},
        next_disbaled: true,
        rules:{
          thumb: [
            { required: true, message: '请选择图片', trigger: 'blur' },
          ],
          name: [
            { required: true, message: '请输入商品名称', trigger: 'blur' },
          ],
          energy: [
            { required: true, message: '请输入能量', trigger: 'blur' },
          ],
          stock: [
            { required: true, message: '请输入商品库存', trigger: 'blur' },
          ],
        },
      }
    },
    created() {
      this.goods.type = 2
      if (this.$route.params.id) {
        this.goods.id = this.$route.params.id
        this.getGoodsInfo()
      }
    },
    methods: {
      // 商品信息
      getGoodsInfo() {
        GoodsApi.info(this.goods.id).then((res) => {
          this.next_disbaled = false
          this.goods = res.goods
          this.goods.content = this.goods.content ? this.goods.content : ""
          this.goods.frozen = Boolean(this.goods.frozen)
        }, (err) => {
          this.$message.error(err.msg);
        })
      },

      save() {
        this.$refs.EditForm.validate((valid) => {
          if (valid) {
            this.btn_loading = true
            GoodsApi.save(this.goods).then((res) => {
              this.btn_loading = false
              if (res.code === 0) {
                this.$message.success('保存成功！');
                this.goods.id = res.goodsId
                this.activeName = 'second'
                this.next_disbaled = false
              } else {
                this.$message.error(res.msg);
              }
            }, (err) => {
              this.btn_loading = false
              this.$message.error(err.msg);
            })
          }
        });
      },
      saveDesc() {
        this.btn_desc_loading = true
        GoodsApi.save({
          id: this.goods.id,
          content: this.goods.content
        }).then((res) => {
          this.btn_desc_loading = false
          if (res.code === 0) {
            this.$message.success('保存成功！');
            this.activeName = 'first'
            this.back()
          } else {
            this.$message.error(res.msg);
          }
        }, (err) => {
          this.btn_desc_loading = false
          this.$message.error(err.msg);
        })
      },
      // 确认图片
      confirmPhoto(pics) {
        this.goods.thumb = pics[0]
      },

      // 更新详情内容
      updateData(content) {
        this.goods.content = content
      },

      // 返回
      back(){
        this.$router.push({path: `/store/manager/cent`})
      },

      ...mapMutations([
        'showUpload'
      ]),
      confirmEditorImg(imgs) {
        this.showUpload(false)
        console.log(imgs)
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        // 异步上传图片
        GoodsApi.uploadPic(imgs).then((res) => {
          loading.close()
          if (res.picArr) {
            res.picArr.forEach(pic => {
              this.$refs.editor.execCommand("insertImage", pic)
            })
          }
        }, (err) => {
          loading.close()
          this.$message.error(err.msg);
        })
      },
      switchEditorShow(val) {
        this.showUpload(val)
      }
    }
  }
</script>

<style lang="scss">
  .goods-add-content {
    .form__button {
      font-size: 12px;
      color: #38f;
      margin-left: 5px;
    }
  }
</style>



