<template>
  <div>
    <common-template>
      <h1 class="content__title" slot="title">{{$route.meta.title}}</h1>

      <div class="content__main-operation" slot="header">
        <div class="operation-left">
          <el-button type="primary" size="small" @click="add">新建分类</el-button>
          <el-button size="small" plain @click="batchDelete">批量删除</el-button>
        </div>
        <div class="operation-right">
          <el-select v-model="req.type" size="small" placeholder="全部类型" @change="getCategoryList">
            <el-option :value="0" label="全部类型"></el-option>
            <el-option :value="1" label="文章类型"></el-option>
            <el-option :value="2" label="视频类型"></el-option>
          </el-select>
          <el-input size="small" placeholder="分类名称" v-model="filterText">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </div>
      </div>

      <div slot="list">
        <el-tree
          style="width: 500px"
          v-loading="!list"
          class="filter-tree"
          :data="list"
          show-checkbox
          node-key="id"
          default-expand-all
          :expand-on-click-node="false"
          :render-content="renderContent"
          :filter-node-method="filterNode"
          @check-change="handleCheckChange"
          ref="trees">
        </el-tree>
      </div>
    </common-template>

    <!-- 添加、编辑 -->
    <el-dialog
      title="分类信息"
      :visible.sync="editVisible"
      width="40%">
      <el-form ref="form" :model="category" :label-width="formLabelWidth">
        <el-form-item label="分类图片">
          <ul class="app-image-list">
            <li class="image-item" v-if="category.icon" @click="showPhotoDialog=true">
              <img :src="category.icon" role="presentation" alt="" width="40">
            </li>
            <li @click="showPhotoDialog=true" v-else class="el-upload el-upload--picture-card" style="width: 40px;height: 40px;line-height: 1;">
              <i class="el-icon-plus" style="font-size: 20px;margin-top: 10px;"></i>
            </li>
          </ul>
        </el-form-item>

        <el-form-item label="分类名称" prop="name" required>
          <el-input size="small" v-model="category.name"></el-input>
        </el-form-item>

        <!--<el-form-item label="选择上级" required>-->
          <!--<el-select size="small" v-model="category.pid" placeholder="选择上级分类">-->
            <!--<el-option value="0" label="顶级分类">== 顶级分类 ==</el-option>-->
            <!--<el-option v-for="item,index in choose_list" v-bind:key="index" :label="(item.tip + item.name)" :value="item.id"></el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
        <el-form-item label="类型" >
          <div>
            <el-radio-group v-model="category.type" :disabled="category.id!=null">
              <el-radio :label="1"><span style="font-weight: normal">文章类型</span></el-radio>
              <el-radio :label="2"><span style="font-weight: normal">视频类型</span></el-radio>
            </el-radio-group>
          </div>
        </el-form-item>

        <el-form-item label="排序字段" prop="sort" required>
          <el-input size="small" v-model="category.sort"></el-input>
          <p class="xs-assist-label">按数值大小升序，越小排在越前</p>
        </el-form-item>

        <el-form-item label="显示状态" >
          <el-switch v-model="category.frozen" :active-text="category.frozen ? '显示' : '隐藏'"></el-switch>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="editVisible = false">取 消</el-button>
        <el-button size="small" type="primary" @click="save">确 定</el-button>
      </span>
    </el-dialog>

    <upload-photo
      :limit="1"
      action = "change"
      :show.sync="showPhotoDialog"
      @confirm="confirmPhoto"
    ></upload-photo>

  </div>

</template>

<script>

  import CommonTemplate from '@/components/common-template'
  import UploadPhoto from 'base/upload-photo/upload-photo.vue';
  import CategoryApi from '@/api/energy.category.js'

  export default {
    name: 'category-list',
    components: {CommonTemplate, UploadPhoto},
    watch: {
      filterText(val) {
        this.$refs.trees.filter(val);
      }
    },
    data () {
      return {
        showPhotoDialog: false,
        editVisible: false,
        filterText: '',
        formLabelWidth: '100px',
        category: {frozen: 1},
        list: null,
        choose_list: [],
        batchArr: [],
        req: {type : 0}
      }
    },
    created() {
      this.getCategoryList()
    },
    methods: {
      renderContent(h, { node, data, store }) {
        return (
          <div class="custom-tree-node">
            <img src={node.data.icon} width="20" />
            <span>{node.data.name}</span>
            <span>{node.data.type==1?'文章类型':'视频类型'}</span>
            <div>
              <el-button size="mini" type="text" on-click={ () => this.edit(node.data.idx) }>修改</el-button>
              <el-button size="mini" type="text" on-click={ () => this.deleteOne(node.data.id) }>删除</el-button>
            </div>
          </div>);
      },

      filterNode(value, data) {
        if (!value) return true;
        return data.name.indexOf(value) !== -1;
      },

      handleCheckChange(data, checked, indeterminate) {
      },

      getCategoryList(){
        this.list = null
        CategoryApi.getCategoryList(this.req).then((res) => {
          this.convertDtoList(res.list)
          this.list = res.list
          this.getChooseList(this.list, 0)
        }, (err) => {
          this.list = []
          this.$message.error(err.msg);
        })
      },
      // 转成tree对象
      convertDtoList(list) {
        if (list) {
          list.forEach(item => {
            if (item.list) {
              item.children = item.list
              item.list = null
              this.convertDtoList(item.children)
            }
          })
        }
      },

      getChooseList: function(list, hr){ // 加载两级分类
        if (!list || list.length===0) return
        if (hr === 0) this.list=[]
        else if (hr === 2) return
        hr += 1
        list.forEach((item, idx) => {
          let childs = item.list
          item.idx = idx
          item.tip = hr === 2 ? "\r\t\r\t" : ""
          item.frozen = Boolean(item.frozen)
          item.list = null
          this.list.push(item)
          if (childs){
            this.getChooseList(item.list, hr)
          }
        })
      },

      add() {
        this.category = {frozen: true, sort: 100, type: 1}
        this.editVisible = true
      },
      edit(idx) {
        this.editVisible = true
        this.category = this.list[idx]
      },
      //  分类图片添加
      confirmPhoto(pics) {
        this.category.icon = pics[0]
      },
      batchDelete() {
        var ids = this.$refs.trees.getCheckedNodes().map(elem => elem.id)
        if (ids.length === 0) {
          this.$message.error("请至少选择一个分类进行删除！");
          return
        }
        this.$confirm('是否确认删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          CategoryApi.deleteCategory(ids).then((res) => {
            this.$message.success('操作成功');
            this.editVisible = false
            this.getCategoryList()
          }, (err) => {
            this.$message.error(err.msg);
          })
        }).catch(() => {

        });
      },
      deleteOne(id) {
        this.$confirm('是否确认删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          CategoryApi.deleteCategory([id]).then((res) => {
            this.$message.success('删除成功');
            this.editVisible = false
            this.getCategoryList()
          }, (err) => {
            this.$message.error(err.msg);
          })
        }).catch(() => {

        });
      },
      save() {
        if (this.category.name==null || this.category.name==""){
          this.$message.error("请输入分类名称");
          return
        }else if(this.category.name.length > 6){
          this.$message.error("分类名称不能超过5个字");
          return
        }
        if (this.category.sort == "") this.category.sort=100
        CategoryApi.saveCategory(this.category).then((res) => {
          this.$message.success('保存成功');
          this.editVisible = false
          this.getCategoryList()
        }, (err) => {
          this.$message.error(err.msg);
        })
      },

    }

  }
</script>

<style lang="scss">
  @import "~common/style/adapter.config";
  .custom-tree-node {
  @include flex($al:center);
    width: 100%;

  > span {
    @include flex-full();
      margin-left: 10px;
    }
  }
</style>



