
import http from '@/utils/http'


export default {

  /**
   * 列表
   */
  getList : () => {
    return http.post("/tag/list", {})
  },

  /**
   * 保存
   * @param params
   * @returns {*|any}
   */
  save : params => {
    return http.post("/tag/save", params)
  },

}



