/**
 * 分类
 */
import http from '@/utils/http'

export default {

  // 列表
  getCategoryList : params => {
    return http.post("/category/list", params)
  },

  // 保存
  saveCategory : params => {
    return http.post("/category/save", params)
  },

  // 批量
  deleteCategory : ids => {
    return http.post("/category/delete", ids)
  },
}



